<template>
  <div class="row">
    <div class="title">
      代码检查
    </div>

    <div class="confVal">
      <a-button v-if="!isEslintStandard" class="config_eslint_set" type="primary" size="small" @click="clickEslintStandard">
        更新ESlint至标准
      </a-button>
      <a-alert v-if="isEslintStandard" class="msg" message="当前处于Eslint处于制造RMS事业部前端标准, 无需操作" type="success" />
      <a-alert v-else class="msg" message="注意" description="当前Eslint文件与制造RMS事业部前端标准不一致" type="warning" />
    </div>
  </div>
</template>

<script setup>
import { computed } from 'vue'
import { useStore } from 'vuex'
import { httpServer } from '@/apis'

// store依赖数据查询
const store = useStore()

const isEslintStandard = computed(() => {
  return store.state.vscodeState.isSyncEslinter
})

// event
const clickEslintStandard = () => {
  httpServer('syncEslintrc', {}, () => {
    store.dispatch('changeVscodeState', { isSyncEslinter: true })
  })
}
</script>

<style scoped lang="scss">
  @import "./main.scss";

  .config_eslint_set {
    width: 150px;
    margin-bottom: 10px;
  }

  .msg {
    max-width: 500px;
  }
</style>
