// 代码测试相关
import getAllConfigApi from './api_test_data/getAllConfigApi'
import syncEslintrc from './api_test_data/syncEslintrc'
import updateI18nData from './api_test_data/updateI18nData'
import changeProjectId from './api_test_data/changeProjectId'
import updateProjectI18nData from './api_test_data/updateProjectI18nData'

const handlerMessageCallback = {}

export function apiToMessage(messageCallback) {
  handlerMessageCallback.fn = messageCallback
}

const Apis = { getAllConfigApi, syncEslintrc, updateI18nData, changeProjectId, updateProjectI18nData }
export function postMessage({ uid, url, data }) {
  const api = Apis[url]

  if (api) {
    api.handler(data, function(sendData) {
      handlerMessageCallback.fn({
        data: {
          uid,
          status: 0,
          data: sendData,
          message: 'success'
        }
      })
    })
  } else {
    handlerMessageCallback.fn({
      data: {
        uid,
        status: 1,
        message: '没有可用的通信API'
      }
    })
  }
}
