<template>
  <div class="row">
    <div class="title">
      当前项目
    </div>

    <div class="confVal">
      <!-- eslint-disable -->
        <a-select
          v-model:value="projectCode"
          size="small"
          class="config_projectCode_select"
        >
          <a-select-option
            v-for="item in projectCodeDict"
            :key="item.identification"
            :value="item.identification"
          >
            {{ item.name }}
          </a-select-option>
        </a-select>
        
        <a-button class="config_projectCode_get" type="primary" size="small" @click="matchProjectCode">识别</a-button>
    </div>
  </div>
  <div class="row projectInfo" v-if="vscodeState.gitConfig">
    <div class="content">
      <div class="contentItem">
        <span class="title">当前分支:</span>
        <span class="value">{{ vscodeState.gitConfig.branch }}</span>
      </div>
      <div class="contentItem">
        <span class="title">项目地址:</span>
        <span class="value">{{ vscodeState.gitConfig.remoteList[0].remoteUrl }}</span>
      </div>
      
    </div>
  </div>
</template>

<script setup>
// 在 script setup 中, 所有定义的变量都可以被 template 识别
import { defineProps, defineEmits, toRefs, ref, computed, watch } from 'vue'
import { httpServer } from '@/apis'
import { useStore } from 'vuex'
// ======== 外部数据 ==================
// props 的定义和获取
const props = defineProps({ value: String })
const { value } = toRefs(props)

// 向外抛出事件
const emit = defineEmits(['update:value'])
// store依赖数据查询
const store = useStore()

// computed
const vscodeState = computed(() => store.state.vscodeState)
const projectCodeDict = computed(() => store.state.projectCodeDict)
const curGitProjectName = computed(() => {
  if (vscodeState.value.gitConfig) {
    const remoteUrl = vscodeState.value.gitConfig.remoteList[0].remoteUrl
    const findItem = projectCodeDict.value.find(item => {
      const { http, ssh } = item.git
      return ssh === remoteUrl || http === remoteUrl
    })

    if (findItem) {
      return findItem.identification
    }
  }
  return ''
})
// ========= data数据 ====================
// 定义data数据
const projectCode = ref(value.value)

if (store.state.vscodeState.geekExpandConfig) {
  projectCode.value = store.state.vscodeState.geekExpandConfig.projectId
}

// watch
watch(projectCode, () => {
  emit('update:value', projectCode.value)
  httpServer('changeProjectId', projectCode.value, () => {
    store.dispatch('setProjectId', projectCode.value)
  })
})

// event
const matchProjectCode = () => {
  projectCode.value = curGitProjectName.value
  httpServer('changeProjectId', projectCode.value, () => {
    store.dispatch('setProjectId', projectCode.value)
  })
}
</script>

<style scoped lang="scss">
  @import "./main.scss";

  .config_projectCode_select {
    width: 150px;
  }

  .config_projectCode_get {
    margin-left: 15px;
    border-radius: 0px;
    width: 60px;
    font-size: 12px;
  }

  .projectInfo {
    .content {
      padding: 5px;
      width: 100%;
      max-width: 500px;
      margin-left: 100px;
      border: 1px solid #eee;
      background: #000;
      color: #fff;
      font-size: 12px;

      .contentItem {
        display: flex;
        flex-direction: row;
        margin-bottom: 10px;

        .title {
          width: 80px;
        }

        .value {
          flex: 1;
        }
      }

      .remoteInput {
        margin-bottom: 5px;
      }
    }
  }
</style>
