import { postMessage, apiToMessage } from './api.test'

const callbacks = {} // 存放所有的回调函数

let vscode
try {
  // eslint-disable-next-line no-undef
  vscode = acquireVsCodeApi()
} catch (error) {
  vscode = {
    isNotVsCode: true,
    postMessage
  }
}

console.info('[api] vscode >', vscode)

export function httpServer(url, data, callback) {
  // 时间戳加上5位随机数
  const uid = Date.now() + '' + Math.round(Math.random() * 100000)
  console.info('[api] sendMessage =>', { url, data, uid })
  callbacks[uid] = callback
  vscode.postMessage({ url, data, uid })
  console.info('[api] sendMessage over')
}

export function initApis() {
  function handlerMessage(event) {
    console.info('[api] message =>', event)
    const message = event.data
    if (message && callbacks[message.uid]) {
      if (message.status === 0) {
        callbacks[message.uid](message.data)
        delete callbacks[message.uid]
      } else {
        // TODO: 失败
      }
    }
  }

  if (vscode.isNotVsCode) {
    apiToMessage(handlerMessage)
  } else {
    window.addEventListener('message', event => {
      handlerMessage(event)
    })
  }
}
