<template>
  <!-- eslint-disable -->
  <a-modal v-model:visible="visible" title="更新i18n对照表">
    <a-upload-dragger
      class="i18nUploadDragger"
      accept=".json,"
      :customRequest="customRequest"
      @change="handleChange"
    >
      <p class="ant-upload-text">点击或拖拽文件到这里进行上传</p>
      <p class="ant-upload-hint">
        支持提交一个中文国际化对照JSON文件
      </p>
      <p v-if="fileData.uid" class="fileDataName">{{ fileData.name }}</p>
    </a-upload-dragger>

    <template #footer>
        <a-button key="back" :loading="loadJson" @click="handleCancel">取消</a-button>
        <a-button key="submit" type="primary" :loading="loadJson" @click="handleOk">确定</a-button>
      </template>
  </a-modal>
</template>

<script>
import { httpServer } from '@/apis'
import { mapActions } from 'vuex'
console.log(httpServer)
export default {
  components: {

  },
  props: {

  },
  data() {
    return {
      fileData: {},
      loadJson: false,
      jsonData: {}
    }
  },
  computed: {

  },
  watch: {

  },
  created() {

  },
  mounted() {

  },
  methods: {
    ...mapActions(['changeVscodeState']),

    customRequest() {
    },

    handleChange({ file }) {
      this.loadJson = true
      this.fileData = file
      const reader = new FileReader()
      reader.readAsText(file.originFileObj)
      reader.onload = () => {
        const jsonData = JSON.parse(reader.result)
        this.loadJson = false
        httpServer('updateI18nData', jsonData, () => {
          this.changeVscodeState({ i18nData: jsonData })
          this.handleCancel()
        })
      }
    },
    handleCancel() {
      this.$emit('update:visible', false)
    },
    handleOk() {
      this.$emit('ok', this.fileData)
    }
  }
}
</script>

<style lang="scss">
.i18nUploadDragger {
  .ant-upload-list {
    display: none;
  }
}
</style>
