<template>
  <div class="i18nCheck">
    <div>
      <!-- eslint-disable -->
      <a-input-search
        class="searchInput"
        v-model:value="searchVal"
        placeholder="输入key或中文进行检索"
        enter-button
      />
    </div>

    <div class="updateBtns">
      <a-button class="projectI18nPull" v-if="['ark'].includes(curProjectId)" type="primary" @click="updateProjectI18nData">{{ curProjectId }} 服务器拉取</a-button>
      <a-button type="primary" @click="updateI18nBtnClick">更新i18n对照表</a-button>
    </div>

    <a-table
      size="small"
      :columns="columns"
      :data-source="curTableList"
      bordered
      :row-class-name="(_record, index) => (index % 2 === 1 ? 'table-striped' : null)"
    />

    <!-- 更新弹出层 -->
    <UpdateI18nMapList v-model:visible="updateI18nMapListVisble" v-if="updateI18nMapListVisble" />
  </div>
</template>

<script>
import UpdateI18nMapList from './components/updateI18nMapList.vue'
import { httpServer } from '@/apis'
import { mapActions, mapState } from 'vuex'

export default {
  components: { UpdateI18nMapList },
  props: {},
  data() {
    return {
      searchVal: '',
      updateI18nMapListVisble: false,
      columns: [
        { title: '中文对照', dataIndex: 'zh', width: '300px', key: 'zh' },
        { title: 'key对照', dataIndex: 'key', width: '300px', key: 'key' }
      ]
    }
  },
  computed: {
    ...mapState(['vscodeState', 'curProjectId']),
    tableOption() {
      const { i18nData } = this.vscodeState
      const list = []
      if (i18nData) {
        Object.keys(i18nData).forEach(key => {
          list.push({
            key,
            zh: i18nData[key]
          })
        })
      }

      return list
    },

    curTableList() {
      const { tableOption, searchVal } = this
      return tableOption.filter((item) => {
        return item.key.includes(searchVal) || item.zh.includes(searchVal)
      })
    }
  },
  watch: {},
  created() {},
  mounted() {},
  methods: {
    ...mapActions(['updateState']),
    updateI18nBtnClick() {
      this.updateI18nMapListVisble = true
    },
    updateProjectI18nData() {
      httpServer('updateProjectI18nData', {}, () => {
        this.updateState('i18nData')
      })
    }
  }
}
</script>

<style scoped lang="scss">
.i18nCheck {
  width: 100%;
  height: 100%;
  text-align: left;
  padding: 10px;

  .searchInput {
    margin-bottom: 10px;
  }
}
</style>

<style lang="scss">
.i18nCheck {
  .resultStyle {
    .ant-result-title, .ant-result-subtitle {
      color: #FFF;
    }
  }

  .updateBtns {
    text-align: right;
    margin-bottom: 10px;
  }
}

.ant-table-striped :deep(.table-striped) td {
  background-color: #fafafa;
}

.projectI18nPull {
  margin-right: 10px;
}
</style>

