<template>
  <div class="menuList">
    <div
      v-for="item in menuList"
      :key="item.path"
      class="menuItem"
      :class="{ active: value === item.componentName }"
      @click="clickItem(item)"
    >
      <p v-if="item.meta.title" class="title">
        {{ item.meta.title }}
      </p>
      <p v-if="item.meta.subTitle" class="subTitle">
        {{ item.meta.subTitle }}
      </p>
    </div>
  </div>
</template>

<script>
import routerMapList from '@/router/menuRouter'

export default {
  components: {},
  props: { value: String },
  data() {
    return {
      routerMapList
    }
  },
  computed: {
    menuList() {
      return this.routerMapList
    }
  },
  watch: {
  },
  created() {},
  mounted() {},
  methods: {
    clickItem(item) {
      this.$emit('update:value', item.componentName)
    }
  }
}
</script>

<style scoped lang="scss">
.menuList {
  text-align: left;
  padding-top: 8px;

  .menuItem {
    margin-top: 2px;
    padding: 5px 20px;
    border-left: 5px solid #ddd;
    background: #37373d;
    min-width: 160px;
    cursor: pointer;

    .title {
      margin: 0;
      padding: 0;
      color: #ddd;
      font-size: 14px;
    }

    .subTitle {
      margin: 0;
      padding: 0;
      color: #ddd;
      font-size: 12px;
    }

    &.active {
      border-left: 5px solid #FFF;
      background: #6b6a6a;

      .title {
        color: #FFF;
      }

      .subTitle {
        color: #FFF;
      }
    }
  }
}
</style>
