import { httpServer } from '@/apis'
import { createStore } from 'vuex'
import { projectCodeDict } from './state/project.state'

export default createStore({
  state: {
    curProjectId: '',
    projectCodeDict,
    vscodeState: {}
  },
  mutations: {
    setVscodeState(state, option) {
      state.vscodeState = option
      const { geekExpandConfig } = option
      if (geekExpandConfig.projectId) {
        state.curProjectId = geekExpandConfig.projectId
      }
    },
    setProjectId(state, projectId) {
      state.curProjectId = projectId
    },
    changeVscodeState(state, option) {
      state.vscodeState = { ...state.vscodeState, ...option }
      const { geekExpandConfig } = state.vscodeState
      if (geekExpandConfig.projectId) {
        state.curProjectId = geekExpandConfig.projectId
      }
    }
  },
  actions: {
    setVscodeState({ commit }, option) {
      commit('setVscodeState', option)
    },

    setProjectId({ commit }, projectId) {
      commit('setProjectId', projectId)
    },

    updateState({ commit }, type) {
      httpServer('updateVscodeState', { type }, function(option) {
        commit('setVscodeState', option)
      })
    },

    changeVscodeState({ commit }, option) {
      commit('changeVscodeState', option)
    }
  },
  modules: {}
})
